                    <span>{{ 'ID: ' + referral.id }}</span>
                </div>
            </div>
        </div>

        <div class="row-2">
            <div class="column-2">
                <div class="column-title">
                    {{ $localizeFilter('sending_date') }}:
                </div>
                <div class="column-value">
