<template>
    <div class="modal-layout">
        <div class="darken" @click="closeModal">
            <div class="new-modal full" @click.stop="">
                <div class="new-modal-back-button-container">
                    <button class="new-modal-back-button thin" @click="closeModal"></button>
                </div>
                <div class="modal-content">
                    <div class="window-title">{{ $localizeFilter('referral_credit_deposit_return_title') }}</div>
                    <div class="window-text">{{ $localizeFilter('referral_credit_deposit_return_text', [referralInfoString]) }}</div>
                    <div class="button-block">
                        <button class="action-button" @click="returnReferralDeposit()">
                            {{ $localizeFilter('return_referral_credit_deposit')}}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    props: {
        referral: {
            type: Object,
            required: true,
        },
    },
    setup(props) {
        console.log('props', props)
        const referral = JSON.parse(JSON.stringify(props.referral))
        console.log('referral', referral)
        const referralInfoString = `ID: ${referral.id} ${referral.name}` 

        return { referralInfoString}

    },
    data: () => ({
        show: 1,
    }),
    watch: {
        referral: function (newVal, oldVal) {
            if (newVal) {
                this.referralInfoString = this.getReferralInfoString()
            }
        }
    },
    methods: {
        closeModal() {
            this.$emit('closeModal', true)
        },
        async returnReferralDeposit() {
            const data = {referralId: this.referral.id}
            try {
                await this.$store.dispatch('returnSponsorCreditDeposit', data)
                this.$message(localizeFilter('referral_credit_deposit_returned'))
                this.closeModal()
            }
            catch (e) {
                this.$error(e)
            }
        },
    },
})
</script>

